import React, { Component, Fragment } from 'react';
import { ValidatorForm } from 'react-material-ui-form-validator';
import FieldHeader from '../shared/FieldHeader';
import { addCustomValidationRules } from '../shared/customValidations';
import PaymentMethod from './fields/idc4Fields/PaymentMethod';
import GuaranteeMethod from './fields/idc4Fields/GuaranteeMethod';
import guaranteeMethodsData from './fields/idc4Fields/data/guaranteeMethodsData';
import guaranteeTypesData from './fields/idc4Fields/data/guaranteeTypesData';
import GuaranteeType from './fields/idc4Fields/GuaranteeType';
import BankFields from './fields/idc4Fields/BankFields';

class ImportDeclarationControl4 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      paymentMethod: '',
      guaranteeMethod: '',
      guaranteeMethodData: [],
      guaranteeTypeData: [],
      guaranteeType: [],
    };
  }

  componentWillMount() {
    addCustomValidationRules(this);
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value,
    });
  };

  chooseDataValue = choosingArray => {
    this.setState({
      guaranteeTypeData: guaranteeTypesData.filter(val =>
        choosingArray.includes(val.value)
      ),
    });
  };

  filterGuaranteeType = event => {
    const { chooseDataValue } = this;
    const guaranteeMethod = event.target.value;
    const payment = document.querySelector('#paymentMethod')
      .value;

    if (guaranteeMethod === 'L') {
      this.setState({
        guaranteeTypeData: [],
      });
      return;
    }

    if (payment === 'A') {
      if (guaranteeMethod === 'A') {
        chooseDataValue(['C', 'B', 'M']);
      } else if (guaranteeMethod === 'H') {
        chooseDataValue(['E']);
      }
    } else if (payment === 'H') {
      if (guaranteeMethod === 'H') {
        chooseDataValue(['C', 'E']);
      }
    } else if (payment === 'L') {
      if (guaranteeMethod === 'A') {
        chooseDataValue(['C', 'B', 'M']);
      } else if (guaranteeMethod === 'H') {
        chooseDataValue(['E', 'C']);
      }
    }
  };

  filterGuaranteeMethods = event => {
    if (event.target.value === 'H') {
      this.setState({
        guaranteeMethodData: guaranteeMethodsData.filter(
          data => data.value !== 'A'
        ),
      });
    } else {
      this.setState({
        guaranteeMethodData: guaranteeMethodsData,
      });
    }
  };

  handleGuaranteeMethodChange = event => {
    this.setState({
      [event.target.name]: event.target.value,
    });
    this.filterGuaranteeType(event);
  };

  handlePaymentMethodChange = event => {
    this.setState({
      [event.target.name]: event.target.value,
    });
    this.filterGuaranteeMethods(event);
  };

  handleSubmit = event => {
    event.preventDefault();
    const data = new FormData(event.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    const {
      handleChange,
      handleSubmit,
      handlePaymentMethodChange,
      handleGuaranteeMethodChange,
    } = this;
    const {
      paymentMethod,
      guaranteeMethod,
      guaranteeMethodData,
      guaranteeType,
      guaranteeTypeData,
    } = this.state;
    return (
      <Fragment>
        <ValidatorForm
          onSubmit={handleSubmit}
          noValidate
          autoComplete="off"
        >
          <FieldHeader>การชำระเงิน</FieldHeader>
          <PaymentMethod
            handleChange={handlePaymentMethodChange}
            paymentMethod={paymentMethod}
          />
          <GuaranteeMethod
            handleChange={handleGuaranteeMethodChange}
            guaranteeMethod={guaranteeMethod}
            guaranteeMethodData={guaranteeMethodData}
          />
          <GuaranteeType
            handleChange={handleChange}
            guaranteeType={guaranteeType}
            guaranteeTypeData={guaranteeTypeData}
          />
          <BankFields
            handleChange={handleChange}
            guaranteeType={guaranteeType}
            guaranteeMethod={guaranteeMethod}
            paymentMethod={paymentMethod}
          />
          {this.props.children}
        </ValidatorForm>
      </Fragment>
    );
  }
}

export default ImportDeclarationControl4;
