import React, { Component, Fragment } from 'react';
import CustomsBankCode from './CustomsBankCode';
import BankCode from './BankCode';
import BankBranchCode from './BankBranchCode';
import BankAccountNumber from './BankAccountNumber';

class BankFields extends Component {
  constructor(props) {
    super(props);
    this.state = {
      customsBankCode: '',
      bankCode: '',
      bankBranchCode: '',
      bankAccountNumber: '',
    };
    this.shouldBeVisible = this.shouldBeVisible.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value,
    });
  }

  shouldBeVisible() {
    const {
      guaranteeMethod,
      paymentMethod,
      guaranteeType,
    } = this.props;

    if (
      paymentMethod === 'H' ||
      (guaranteeMethod === 'H' && guaranteeType === 'C')
    ) {
      return true;
    } else {
      return false;
    }
  }

  render() {
    const {
      customsBankCode,
      bankCode,
      bankBranchCode,
      bankAccountNumber,
    } = this.state;

    const { handleChange } = this;
    return this.shouldBeVisible() ? (
      <Fragment>
        <CustomsBankCode
          handleChange={handleChange}
          customsBankCode={customsBankCode}
        />
        <BankCode
          handleChange={handleChange}
          bankCode={bankCode}
        />
        <BankBranchCode
          handleChange={handleChange}
          bankBranchCode={bankBranchCode}
        />
        <BankAccountNumber
          handleChange={handleChange}
          bankAccountNumber={bankAccountNumber}
        />
      </Fragment>
    ) : (
      ''
    );
  }
}

export default BankFields;
